import React, {
  useRef,
  useEffect,
  FC,
  memo,
} from 'react';
import LoadingBar from 'react-top-loading-bar';

interface LoadingBarProps {
  continuousStart: () => void;
  complete: () => void;
}

interface PropsType {
  loading: boolean;
}

const TopLoading: FC<PropsType> = ({ loading }) => {
  const ref = useRef<HTMLDivElement & LoadingBarProps | null>(null);

  const handleContinuousStart = () => {
    if (ref.current) ref.current.continuousStart();
  };

  const handleComplete = () => {
    if (ref.current) ref.current.complete();
  };

  useEffect(() => {
    if (loading) {
      handleContinuousStart();
    } else {
      handleComplete();
    }
  }, [loading]);

  return <LoadingBar color="#f6bb3d" ref={ref} />;
};

export default memo(TopLoading);
